.content-tab {
    background-color: var(--tab-background-color);
    position: relative;
    display: flex;
    flex-direction: column;
    width: 23%;
    margin: 0.25%;
    border: 1px solid var(--border-color);
    ;
    border-radius: 5px;
    box-sizing: border-box;
    color: black;
    transform: scale(100%);
    transition: transform 300ms;
}

.content-tab:hover {
    transform: scale(102.5%);
    transition: transform 100ms;
}

.content-tab > * > .content-tab-image {
    width: 100%;
    /* border-bottom: 1px solid black; */
    border-radius: 5px;
    box-shadow: 0px 2px 3px var(--tag-background-color);
}

.content-tab > .content-tab-title {
    color: var(--forground-color-2);
    position: absolute;
    top: 0%;
    left: 5%;
    font-size: 1.5vw;
}

.content-tab > .content-description {
    color: var(--forground-color-1);
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    justify-content: space-between;
    padding: 2%;
    box-sizing: border-box;
    margin-bottom: 10%;
}

.content-tab > .content-tab-tags {
    position: relative;
    color: var(--forground-color-1);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: flex-start;
}

.content-tab > .content-tab-tags > .clickable {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    content: "";
}

.content-tab > .content-tab-tags > .tags {
    padding-left: 3%;
    padding-right: 3%;
    padding-top: 1%;
    padding-bottom: 1%;
    margin: 1%;
    z-index: 0;
    background-color: var(--tag-background-color);
    box-shadow: 2px 3px 3px #0005;
    border: 1px solid var(--border-color);
    ;
    border-radius: 10px;
}

.content-tab > .source-code {
    position: absolute;
    left: 10px;
    bottom: 10px;
    font-size: 0.75rem;
}

@media (max-width: 1220px) {
    .content-tab {
        width: 31%;
    }

    .content-tab > .content-tab-title {
        font-size: 2vw;
    }
}

@media (max-width: 870px) {
    .content-tab {
        width: 46%;
    }

    .content-tab > .content-tab-title {
        font-size: 2.5vw;
    }
}

@media (max-width: 520px) {
    .content-tab {
        width: 95%;
    }

    .content-tab > .content-tab-title {
        font-size: 5vw;
    }
}